.form_control {
  display: flex;
  flex-direction: column;
  margin-bottom: 1em;
}

.form_control label {
  margin-bottom: 0.3em;
  font-weight: bold;
}

.form_control select {
  padding: 0.7em;
  border: 1px solid #777;
  border-radius: 5px;
}

