.message {
  position: fixed;
  width: 70%;
  margin: 2% 20% 0% 20%;
  padding: 1em;
  border: 1px solid #000;
  text-align: center;
  max-width: 60%;
  border-radius: 5px;
  z-index: 15;
}

.success {
  color: #155724;
  background-color: #d4edda;
  border-color: #c3e6cb;
}

.error {
  color: #721c24;
  background-color: #f8d7da;
  border-color: #f5c6cb;
}

/* ###########################         Mobile/Tablet           ##################################### */

@media (max-width: 767px) {
  .message {
    width: 90vw;
    max-width: 90vw;
    margin: 2% 5%;
  }
}