.homeselector_section {
  text-align: center;
}

.heading {
  margin: 2.5em;
  text-align: center;
}

.heading h1 {
  color: #fff;
}

.btns_container {
  display: flex;
  flex-direction: column;
}

.btns_container button {
  border-radius: 8px;
  background-color: #25b456;
  color: #fff;
  border: none;
  width: 350px;
  max-width: 85vw;
  height: 2.5rem;
  cursor: pointer;
  font-size: calc(14px + (18 - 14) * ((100vw - 320px) / (1920 - 320)) );
  font-weight: bold;
  margin: 0.5em auto;
}

.btns_container button:hover {
  background-color: #1c8a42;
}

